.App {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
  box-sizing: border-box;
  text-align: center;
}
.App-logo{
  max-width: 300px;
  margin: 20px auto;
}
.init-btn{
  width: 80px;
  margin: 0 10px;
  background: blueviolet;
  color: #fff;
  text-transform: uppercase;
  border-radius: 5px;
  border: none;
  padding: 4px 0;
  cursor: pointer;
  transition: all .3s;
}
.init-btn:hover{
  background: lightblue;
}
.wrapper{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  flex-direction: column-reverse;
}
.squares-list{
  width: 100%;
  /*max-width: 80%;*/
  margin: 50px auto;
  display: block;
}
.row-items{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hovered-items-info-wrap{
  width: 100%;
}
.hovered-items-info{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  padding: 4px;
  color: #000;
  background: lightgreen;
  width: 50%;
  max-width: 210px;
  margin: auto;
}
.hovered-items-info>span{
  margin-right: 20px;
}
@media (max-width: 450px){
  .squares-list__item {
    width: 30px;
    height: 30px;
  }
}

.squares-list__item{
    display: block;
    width: 40px;
    height: 40px;
    border: 1px solid #000;
}
.active{
    background: blue;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

